/**
 * @desc 
 * @param func 
 * @param wait 
 * @param immediate true -  false - 
 */
function debounce(func: any, wait: number, immediate = true) {
  let timer: any;
  return function () {
    // @ts-ignore
    const context = this;
    const args = arguments; // eslint-disable-line

    if (timer) clearTimeout(timer);
    if (immediate) {
      const callNow = !timer;
      timer = setTimeout(() => {
        timer = null;
      }, wait);
      if (callNow) func.apply(context, args);
    } else {
      timer = setTimeout(() => {
        func.apply(context, args);
      }, wait);
    }
  };
}

/**
 * @desc 
 * @param func 
 * @param wait 
 * @param type 1 2 
 */
function throttle(func: any, wait: number) {
  let previous = 0;
  return function () {
    const now = Date.now();
    // @ts-ignore
    const context = this;
    const args = arguments; // eslint-disable-line
    if (now - previous > wait) {
      func.apply(context, args);
      previous = now;
    }
  };
}

export { debounce, throttle };
